html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 300;
  src: local("Helvetica Light"), local("Helvetica-Light"), url("../fonts/Helvetica-Light.woff2") format("woff2"), url("../fonts/Helvetica-Light.woff") format("woff");
}

@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  src: local("Helvetica Regular"), local("Helvetica-Regular"), url("../fonts/Helvetica-Regular.woff2") format("woff2"), url("../fonts/Helvetica-Regular.woff") format("woff");
}

@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 700;
  src: local("Helvetica Bold"), local("Helvetica-Bold"), url("../fonts/Helvetica-Bold.woff2") format("woff2"), url("../fonts/Helvetica-Bold.woff") format("woff");
}

.icon-pen {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-phone {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #FFF;
  line-height: 1.4;
  font-size: 16px;
  min-width: 320px;
}

a {
  text-decoration: none;
}

.container {
  padding: 0 9px;
  width: 100%;
  max-width: 1282px;
  margin: 0 auto;
}

.out {
  overflow: hidden;
}

html {
  background-color: #1f1f1f;
}

.underline-link {
  border-bottom: 1px solid transparent;
  transition: border 300ms;
}

.no-touch .underline-link:hover {
  text-decoration: none;
  border-bottom: 1px solid black;
}

.no-touch .underline-link_gray:hover {
  border-color: #4d4d4d;
}

.nav__list {
  min-width: 468px;
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 1023px) {
  .nav__list {
    margin-left: 0;
    display: flex;
    flex-direction: column;
  }
}

@media only screen and (max-width: 767px) {
  .nav__list {
    min-width: auto;
    width: 100%;
  }
}

.nav__item {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  transition-duration: 150ms;
}

@media only screen and (max-width: 1023px) {
  .nav__item {
    position: relative;
    padding: 12px 0;
    text-align: center;
  }
  .nav__item:before {
    content: '';
    position: absolute;
    height: 1px;
    top: 0;
    left: 15px;
    right: 15px;
    background-color: #adadad;
  }
}

.nav__link {
  font-weight: 400;
  border-bottom: 1px solid transparent;
  transition: border 300ms;
  color: #fff;
}

.nav__link:hover {
  border-bottom: 1px solid #fff;
}

@media only screen and (max-width: 1023px) {
  .nav__link {
    color: #000;
  }
  .no-touch .nav__link:hover {
    border-bottom: 1px solid #000;
  }
}

@media only screen and (max-width: 767px) {
  .nav__link {
    letter-spacing: -0.4px;
  }
}

.button {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  transition: 300ms;
  display: inline-block;
  font-weight: 700;
  background: transparent;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: 16px;
  color: #fff;
  padding: 12px 70px;
}

.no-touch .button:hover {
  color: #000;
  background-color: #fff;
  border-color: #000;
}

.button_uppercase {
  text-transform: uppercase;
}

.button_purple {
  overflow: hidden;
  position: relative;
  color: #fff;
  background: linear-gradient(135deg, #c32aff 0%, #9013fe 100%);
  border: 1px solid #d8d8d8;
}

.button_purple:before {
  content: '';
  transition: opacity 300ms;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(136deg, #781ca6 0%, #6116a3 100%);
  opacity: 0;
}

.no-touch .button_purple:hover {
  color: #fff;
  border: 1px solid #d8d8d8;
}

.no-touch .button_purple:hover:before {
  opacity: 1;
}

.button_lg {
  font-size: 18px;
  padding: 20px 104px;
}

.title {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  letter-spacing: -2px;
  font-size: 46px;
  font-weight: 700;
  color: #000;
  position: relative;
}

@media only screen and (max-width: 767px) {
  .title {
    font-size: 28px;
  }
}

.title_decorated {
  padding-bottom: 24px;
}

.title_decorated:after {
  content: '';
  position: absolute;
  width: 54px;
  height: 8px;
  background-color: #000;
  left: 2px;
  bottom: 0;
}

.title_white {
  color: #fff;
}

.title_white:after {
  background-color: #fff;
}

.title_sm {
  font-size: 23px;
  letter-spacing: .25px;
}

@media only screen and (max-width: 767px) {
  .title_sm {
    font-size: 20px;
  }
}

.process {
  overflow: hidden;
  padding: 30px 0 25px;
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  width: calc(50% - 8px);
  max-width: 554px;
  position: relative;
}

@media only screen and (max-width: 767px) {
  .process {
    width: 100%;
    max-width: none;
    padding: 8px 12px;
  }
}

.no-touch .process:hover .process__head:after {
  max-width: 100%;
}

.process__head {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  transition-duration: 300ms;
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 10px;
}

.process__head:after {
  transition: 300ms;
  content: '';
  position: absolute;
  width: 100%;
  max-width: 56px;
  height: 1px;
  background-color: #979797;
  left: 0;
  bottom: 0;
}

@media only screen and (max-width: 767px) {
  .process__head:after {
    max-width: 36px;
  }
}

.process__count {
  transition: 300ms;
  position: relative;
  font-size: 50px;
  font-weight: 700;
  color: #9013fe;
}

@media only screen and (max-width: 767px) {
  .process__count {
    font-size: 32px;
  }
}

.process__title {
  transition: 300ms;
  margin-left: 18px;
}

.process__descr {
  letter-spacing: 0.002em;
  text-align: justify;
  word-spacing: 0.02em;
  line-height: 1.86;
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  transition-duration: 300ms;
  font-size: 14px;
  position: relative;
  color: #646464;
}

.project {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  overflow: hidden;
  will-change: transform;
  transform: rotateX(0deg) rotateY(0deg);
  transition: transform 100ms;
  width: calc(50% - 8px);
  max-width: 600px;
  margin-top: 30px;
  border-radius: 10px;
  min-height: 328px;
  height: 328px;
}

@media only screen and (max-width: 767px) {
  .project {
    width: 100%;
    max-width: 100%;
  }
}

.project_active {
  transition: none;
}

.project .project__background {
  position: absolute;
  transition: width 1000ms;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.project__link {
  top: 0;
  opacity: 1;
  position: relative;
  transition: 1000ms ease;
  color: white;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 28px 40px 44px;
}

@media only screen and (max-width: 1023px) {
  .project__link {
    padding-bottom: 27px;
  }
}

@media only screen and (max-width: 767px) {
  .project__link {
    padding: 17px 22px 27px;
  }
}

.no-touch .project__link:hover .project__see {
  opacity: 1;
}

.project__title {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  pointer-events: none;
}

.project__logo {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  pointer-events: none;
  margin-top: auto;
}

.project__footer {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  pointer-events: none;
  margin-top: 12px;
  display: flex;
  align-items: baseline;
}

.project__date {
  font-size: 16px;
  font-weight: 300;
}

.project__see {
  transition: 300ms;
  margin-left: auto;
  color: white;
  text-decoration: underline;
  opacity: 0;
}

.js-project-anim {
  left: 100px;
  opacity: 0;
}

.value {
  position: relative;
  transition: background 300ms;
  color: #000;
  background-color: #121212;
}

.value:before {
  z-index: 1;
  transition: transform 250ms, opacity 250ms linear;
  content: '';
  position: absolute;
  width: calc((50% - 1242px/2) + 1242px * 0.4);
  background: linear-gradient(135deg, #5a96fa 0%, #59097c 100%);
  height: calc(100% + 1px);
  top: 0;
  right: 0;
  transform: scaleY(0);
  opacity: 0;
}

@media only screen and (max-width: 1272px) {
  .value:before {
    width: 40%;
  }
}

@media only screen and (max-width: 1023px) {
  .value:before {
    width: 30%;
  }
}

@media only screen and (max-width: 767px) {
  .value:before {
    height: 77px;
    width: 100%;
    top: auto;
    bottom: -1px;
  }
}

.no-touch .value:hover:before {
  opacity: 1;
  transform: scaleY(1);
}

.value .value__container:after {
  content: '';
  position: absolute;
  height: 1px;
  top: 0;
  left: 15px;
  right: 15px;
  background-color: #e6e6e6;
}

.value__container {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  transition-duration: 300ms;
  opacity: 1;
  z-index: 1;
  min-height: 250px;
  position: relative;
  display: flex;
  padding: 15px 15px;
}

@media only screen and (max-width: 767px) {
  .value__container {
    flex-wrap: wrap;
  }
}

.value__info {
  width: 60%;
}

@media only screen and (max-width: 1023px) {
  .value__info {
    width: 70%;
  }
}

@media only screen and (max-width: 767px) {
  .value__info {
    width: 100%;
  }
}

.value__title {
  margin-top: 44px;
}

@media only screen and (max-width: 767px) {
  .value__title {
    margin-top: 12px;
  }
}

.value__descr {
  color: #fff;
  margin-top: 40px;
  font-size: 14px;
  line-height: 1.85;
  max-width: 618px;
}

.value__price {
  color: #fff;
  z-index: 2;
  transition: color 300ms;
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1023px) {
  .value__price {
    width: 30%;
    justify-content: flex-end;
    align-items: flex-end;
  }
}

@media only screen and (max-width: 767px) {
  .value__price {
    margin-top: 16px;
    width: 100%;
  }
}

.value__price-text {
  margin-left: 60px;
  margin-top: 20px;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .value__price-text {
    margin-top: 0;
    margin-left: 0;
  }
}

.value__price-value {
  padding: 16px;
  font-size: 34px;
  font-weight: 700;
}

.pen {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  z-index: 6;
  position: fixed;
}

.pen__background {
  pointer-events: none;
  z-index: 1;
  position: fixed;
  width: 0;
  height: 0;
  transform: translateX(50%) translateY(50%);
  background-color: #fff;
  bottom: 55px;
  right: 55px;
  border-radius: 50%;
}

.pen__button {
  transition: transform 300ms;
  transform: scale(1);
  cursor: pointer;
  position: fixed;
  background-color: rgba(66, 66, 66, 0.42);
  width: 62px;
  height: 62px;
  bottom: 24px;
  right: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.no-touch .pen__button:hover {
  transform: scale(1.05);
}

.pen__button:active {
  opacity: .85;
}

@media only screen and (max-width: 767px) {
  .pen__button {
    width: 36px;
    height: 36px;
  }
}

.pen__button_closed {
  transform: scale(0);
}

.pen__image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.pen__image .icon {
  font-size: 25px;
  fill: #fff;
}

@media only screen and (max-width: 767px) {
  .pen__image .icon {
    font-size: 20px;
  }
}

.pen__container {
  position: relative;
}

.pen__content {
  transition: transform 300ms, opacity 300ms;
  transform: translateY(100px);
  pointer-events: none;
  z-index: 6;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-height: 100%;
  overflow: auto;
  background-size: cover;
  opacity: 0;
}

.pen__content_opened {
  transform: translateY(0);
  pointer-events: auto;
  opacity: 1;
}

.pen__close {
  position: absolute;
  display: flex;
  align-items: center;
  top: 16px;
  right: 16px;
  transition-duration: 150ms;
  cursor: pointer;
  margin-left: auto;
  width: 24px;
  height: 24px;
}

.pen__close-icon {
  transition: background-color 300ms;
  position: relative;
  width: 100%;
  height: 2px;
  background-color: transparent;
}

.pen__close-icon:before, .pen__close-icon:after {
  transition-property: top, bottom, transform;
  transition-duration: 300ms;
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #000;
}

.pen__close-icon:before {
  transform: rotate(45deg);
}

.pen__close-icon:after {
  transform: rotate(-45deg);
}

.pen__subtitle {
  margin-right: 40px;
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  transition-duration: 175ms;
  padding-top: 12px;
}

.pen__cta {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  transition-duration: 175ms;
  margin-top: 12px;
  color: #b61df3;
  font-size: 32px;
  font-weight: 700;
}

.form {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  margin-top: 30px;
  transition-duration: 175ms;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form__input {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  transition-property: top, left, opacity, transform, border-color;
  transition-duration: 250ms;
  position: relative;
  font-size: 18px;
  margin-top: 7px;
  min-height: 64px;
  max-width: 660px;
  width: 100%;
  border: 1px solid #d8d8d8;
  border-radius: 5px;
  padding: 20px 24px;
}

.form__input::placeholder {
  font-size: 14px;
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  color: #6c6c6c;
}

.form__input:focus {
  border-color: #9013fe;
}

.form__input_invalid {
  border-color: red;
}

.form__input_invalid + .form__validation-message {
  opacity: 1;
  max-height: 20px;
}

.form__validation-message {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  transition-property: opacity, max-height;
  transition-duration: 300ms;
  max-height: 0;
  opacity: 0;
  color: red;
  font-size: 12px;
  font-weight: 300;
  margin-top: 4px;
}

.form__textarea {
  resize: none;
  padding: 24px 24px;
}

.form__file-label {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  transition-duration: 175ms;
  margin-top: 30px;
  max-width: 494px;
  width: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.form__file-label:before {
  flex-shrink: 0;
  content: '';
  display: block;
  width: 30px;
  height: 38px;
  background: url("../img/input-file.svg") no-repeat center;
  margin-right: 20px;
}

.no-touch .form__file-label:hover {
  text-decoration: none;
  color: rgba(53, 53, 53, 0.7);
}

.form__file-text {
  color: #353535;
}

.form__input-file {
  display: none;
}

.form__policy {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  line-height: 2;
  transition-duration: 175ms;
  margin-top: 20px;
  font-size: 14px;
  font-weight: 300;
  color: #4d4d4d;
}

.form__link {
  color: #4d4d4d;
}

.form__button {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-property: top, left, opacity, transform;
  transition-timing-function: ease-out;
  transition-duration: 350ms;
  position: relative;
  transition-duration: 175ms;
  margin-top: 16px;
}

.form__button-text {
  pointer-events: none;
  position: relative;
}

.form__animate {
  opacity: 0;
  transform: translateY(-32px);
}

.header {
  transition: 500ms;
  position: fixed;
  z-index: 5;
  width: 100%;
  padding-top: 32px;
  padding-bottom: 37px;
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  background-color: transparent;
  font-weight: 700;
}

@media only screen and (max-width: 1023px) {
  .header {
    background-color: #fff;
    font-size: 16px;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.header .js-header-anim {
  opacity: 0;
  transform: translateX(-20%);
}

.header .js-header-anim_logo {
  transform: translateX(0);
}

.header_scrolled {
  padding-top: 0;
  padding-bottom: 0;
  background-color: #fff;
  box-shadow: 0 1px 12px rgba(0, 0, 0, 0.1);
}

.header_scrolled .header__logo .header__logo-image {
  width: 103px;
  height: 37px;
  background: url("../img/logo.png") no-repeat center;
  background-size: cover;
}

.header_scrolled .header__link {
  color: #000;
