.title {
	@include helvetica;
	letter-spacing: -2px;
	font-size: 46px;
	font-weight: 700;
	color: $black;
	position: relative;
	@include sm {
		font-size: 28px; }

	&_decorated {
		padding-bottom: 24px;
		&:after {
			content: '';
			position: absolute;
			width: 54px;
			height: 8px;
			background-color: $black;
			left: 2px;
			bottom: 0; } }

	&_white {
		color: $white;
		&:after {
			background-color: $white; } }

	&_sm {
		font-size: 23px;
		letter-spacing: .25px;
		@include sm {
			font-size: 20px; } } }
