.nav {

	&__list {
		min-width: 468px;
		display: flex;
		justify-content: space-between;
		@include md {
			margin-left: 0;
			display: flex;
			flex-direction: column; }
		@include sm {
			min-width: auto;
			width: 100%; } }

	&__item {
		@include animate;
		transition-duration: 150ms;
		@include md {
			position: relative;
			padding: 12px 0;
			text-align: center;
			&:before {
				content: '';
				position: absolute;
				height: 1px;
				top: 0;
				left: 15px;
				right: 15px;
				background-color: $gray; } } }

	&__link {
		font-weight: 400;
		border-bottom: 1px solid transparent;
		transition: border 300ms;
		color: $white;
		&:hover {
			border-bottom: 1px solid $white; }
		@include md {
			color: $black;
			@include hover {
				border-bottom: 1px solid $black; } }
		@include sm {
			letter-spacing: -0.4px; } } }
