.header {
	transition: 500ms;
	position: fixed;
	z-index: 5;
	width: 100%;
	padding-top: 32px;
	padding-bottom: 37px;
	@include helvetica;
	background-color: transparent;
	font-weight: 700;
	@include md {
		background-color: $white;
		font-size: 16px;
		padding-top: 0;
		padding-bottom: 0; }

	.js-header-anim {
		opacity: 0;
		transform: translateX(-20%); }

	.js-header-anim_logo {
		transform: translateX(0); }

	&_scrolled {
		padding-top: 0;
		padding-bottom: 0;
		background-color: $white;
		box-shadow: 0 1px 12px rgba(0,0,0,.1);
		.header__logo {
			.header__logo-image {
				width: 103px;
				height: 37px;
				background: url("../img/logo.png") no-repeat center;
				background-size: cover; } }
		.header__link {
			color: $black;
			@include hover {
				border-bottom: 1px solid $black; } }
		.header__menu {
			margin-top: 0; }
		.nav__link {
			color: $black;
			@include hover {
				border-bottom: 1px solid $black; } } }

	&__container {
		display: flex;
		align-items: center;
		min-height: 65px;
		height: 65px;
		@include sm {
			justify-content: center;
			flex-wrap: wrap;
			padding: 15px; } }

	&__logo {
		@include animate;
		.header__logo-image {
			width: 145px;
			height: 52px;
			background: url("../img/logo-white.png") no-repeat center;
			background-size: cover; }
		@include md {
			.header__logo-image {
				width: 103px;
				height: 37px;
				background: url("../img/logo.png") no-repeat center;
				background-size: cover; } } }

	&__menu {
		margin-top: 8px;
		top: auto;
		flex-grow: 1;
		display: flex;
		@include md {
			border-bottom: 1px solid $gray;
			box-shadow: 0 2px 4px rgba(0,0,0,.1);
			flex-direction: column;
			width: 100%;
			bottom: 0;
			left: 0;
			position: absolute;
			transform-origin: top center;
			transform: translateY(100%) rotateX(90deg);
			z-index: 1;
			background-color: $white; } }

	&__menu_animate {
		left: 100%;
		opacity: 0; }

	&__nav {
		margin-left: 92px;
		@include md {
			margin-left: 0; } }

	&__feedback {
		display: flex;
		justify-content: flex-end;
		margin-left: auto;
		min-width: 324px;
		@include md {
			margin-left: 0;
			flex-direction: column; }
		@include sm {
			min-width: auto;
			display: flex;
			flex-direction: column; } }

	&__link-wrapper {
		@include animate;
		transition-duration: 150ms;
		@include md {
			display: flex;
			justify-content: center;
			position: relative;
			padding: 12px 0;
			&:before {
				content: '';
				position: absolute;
				height: 1px;
				top: 0;
				left: 15px;
				right: 15px;
				background-color: $gray; } } }

	&__link {
		transition: border 300ms;
		font-size: 16px;
		display: flex;
		align-items: center;
		color: $white;
		border-bottom: 1px solid transparent;
		.icon {
			font-size: 16px; }
		@include hover {
			border-bottom: 1px solid $white; }
		.icon {
			margin-right: 9px; }
		@include md {
			color: $black;
			@include hover {
				border-bottom: 1px solid $black; } } }

	&__toggle {
		transition-duration: 150ms;
		cursor: pointer;
		display: none;
		align-items: center;
		margin-left: auto;
		width: 24px;
		height: 24px;
		@include md {
			display: flex; } }



	&__toggle-icon {
		transition: background-color 300ms;
		position: relative;
		width: 100%;
		height: 2px;
		background-color: $black;

		&:before, &:after {
			transition-property: top, bottom, transform;
			transition-duration: 300ms;
			content: '';
			position: absolute;
			width: 100%;
			height: 2px;
			left: 0;
			background-color: $black; }

		&:before {
			top: -8px; }

		&:after {
			bottom: -8px; } } }

// Opened
.header_opened {

	.header__menu {
		transition: transform 300ms linear;
		transform: translateY(100%) rotateX(0); }

	.header__toggle-icon {
		background-color: transparent;
		&:before {
			top: 0;
			transform: rotate(45deg); }
		&:after {
			bottom: 0;
			transform: rotate(-45deg); } } }
