.main-hero {
	overflow: hidden;
	box-shadow: 0 6px 8px rgba(0, 0, 0, .5);
	@include helvetica;
	color: $white;
	position: relative;
	background-size: cover;

	&:before {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: rgba($darkgray, .8); }

	&__container {
		position: relative;
		height: 100vh;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: center; }

	&__video {
		z-index: 0;
		position: absolute;
		height: 100%;
		width: 100%;
		left: 0;
		top: 0;
		@include sm {
			position: absolute;
			top: 0;
			left: 0; }
		&:before {
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			content: "";
			z-index: 1;
			background: linear-gradient(-44deg, rgba(0, 0, 0, 0.8) 0%, rgba(11, 0, 31, 0.8) 100%); } }

	&__content {
		display: flex;
		flex-direction: column;
		min-height: 308px;
		margin-top: -44px;
		@include md {
			margin-top: 0; } }

	&__image {
		@include animate;
		transition-duration: 500ms;
		content: '';
		pointer-events: none;
		position: absolute;
		background: url("../img/iphone-mac.png") no-repeat calc(100% + 170px) calc(50% + 24px);
		background-size: 50vw;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		@include md {
			display: none; } }

	&__image_animate {
		left: 100px;
		opacity: 0; }


	&__title {
		letter-spacing: .25px;
		transition-duration: 200ms;
		@include animate; }

	&__descr {
		transition-duration: 200ms;
		@include animate;
		margin-top: 24px;
		font-size: 15px;
		font-weight: 300;
		line-height: 2; }

	&__table {
		transition-duration: 200ms;
		margin-top: auto;
		@include animate;
		max-width: 524px; }

	&__table-row {}

	&__table-data {
		line-height: 1;
		text-align: center;
		font-size: 46px;
		font-weight: 700;
		@include sm {
			font-size: 24px; } }


	&__table-head {
		line-height: 2.2;
		text-align: center;
		font-weight: 300;
		font-size: 13px;
		text-transform: uppercase;
		padding: 0 8px; }

	&__button {
		@include animate;
		transition: transform 300ms, opacity 1000ms, background-color 300ms, border 300ms;
		margin-top: 54px;
		@include sm {
			margin-top: 24px; } } }

.js-main-hero-anim {
	transition-duration: 300ms;
	transform: translateY(50px);
	opacity: 0; }
