.portfolio-list {
	background-color: #fafafa;
	padding-top: 30px;
	padding-bottom: 105px;

	&__container {}

	&__title {
		@include animate; }

	&__title_animate {
		opacity: 0;
		transform: translateY(100px); }

	&__list {
		margin-top: 6px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }

	&__item {} }
